.projectsSection {
  background-color: $light;
  padding: 0px 8% 20px 8%;
  display: flex;
  flex-direction: column;
  align-items: center;
  &-title {
    text-align: center;
    @include typo-size($main_font, map-get($font-sizes, sl));
  }
  &-container {
    &-project {
      color: $dark;
      text-decoration: none;
      display: flex;
      flex-direction: column;
      border: 1px solid $dark;
      border-radius: 1px;
      padding: 10px;
      margin: 15px;
      &-name {
        padding: 12px 0;
        text-align: center;
        font-weight: 600;
        @include typo-size($main_font, map-get($font-sizes, xs));
      }
      &-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        &-links {
          display: flex;
          flex-direction: column;
          align-items: center;
          &-img {
            width: 100%;
            height: auto;
            max-width: 200px;
            object-fit: cover;
            border-radius: 5px;
          }
          &-linksContainer {
            width: 100%;
            display: flex;
            justify-content: space-around;
            padding: 20px 0 10px 0;
            &-website {
              background-color: $dark;
              color: $light;
              padding: 7px;
              border-radius: 5px;
              text-decoration: none;
              cursor: pointer;
            }
            &-website:hover {
              @include keyframes(hover) {
                0% {
                  transform: scale(1);
                  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
                  font-weight: 300;
                }

                100% {
                  transform: scale(1.02);
                  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.4);
                  font-weight: 400;
                }
              }
              @include animate(hover, 0.8s, infinite, alternate-reverse);
            }
          }
        }

        &-description {
          text-align: justify;
          width: 100%;
          padding: 10px;
          font-weight: 300;
          @include typo-size($second_font, map-get($font-sizes, xxs));
        }
        &-tags {
          width: 100%;
          height: auto;
          display: flex;
          align-items: center;
          justify-content: space-around;
          span {
            text-align: center;
            width: 100%;
            background-color: $dark;
            color: $light;
            border-radius: 1px;
            padding: 0.2em;
            margin: 0.2em;
            @include typo-size($main_font, map-get($font-sizes, xxs));
          }
        }
      }
    }
  }
}

@media (min-width: map-get($breakpoints, tablet)) {
  .projectsSection {
    &-title {
      @include typo-size($main_font, map-get($font-sizes, l));
    }
    &-container {
      &-project {
        &-name {
          padding: 5px 0;
          font-weight: 400;
          @include typo-size($main_font, map-get($font-sizes, s));
        }
        &-content {
          display: flex;
          flex-direction: row;
          justify-content: space-around;
          &-description {
            text-align: start;
            width: 40%;
            @include typo-size($second_font, map-get($font-sizes, xs));
          }
          &-tags {
            width: 20%;
            display: flex;
            flex-direction: column;
            span {
              padding: 0.3em;
              margin: 0.4em;
              @include typo-size($main_font, map-get($font-sizes, xxs));
            }
          }
        }
      }
    }
  }
}
@media (min-width: map-get($breakpoints, desktop)) {
  .projectsSection {
    padding: 40px 100px;
    &-title {
      @include typo-size($main_font, map-get($font-sizes, xl));
    }
    &-container {
      &-project {
        &-name {
          @include typo-size($main_font, map-get($font-sizes, sl));
        }
        &-content {
          &-links {
            &-img {
              max-width: 280px;
            }
          }
          &-description {
            @include typo-size($second_font, map-get($font-sizes, s));
          }
          &-tags {
            span {
              max-width: 160px;
              @include typo-size($main_font, map-get($font-sizes, xs));
            }
          }
        }
      }
    }
  }
}
