.nav {
  display: none;

  @media (min-width: map-get($breakpoints, tablet)) {
    display: block;
    background-color: $light;
    display: flex;
    padding: 40px 8%;

    &-logo {
      width: 100%;
      max-width: 110px;
    }
    &-links {
      list-style: none;
      display: flex;
      flex-direction: row;
      width: 100%;
      justify-content: space-around;
      align-items: center;
      @include typo-size($main_font, map-get($font-sizes, s));
      &-link {
        text-decoration: none;
        color: $dark;
      }
    }
    &-links:hover {
      cursor: pointer;
    }
    &-links-link:hover {
      color: lighten($dark, 20%);
    }
  }

  @media (min-width: map-get($breakpoints, desktop)) {
    padding: 40px 100px;
    &-logo {
      max-width: 120px;
    }
    &-links {
      @include typo-size($main_font, map-get($font-sizes, l));
    }
  }
}

.burger-nav {
  /* Position and sizing of burger button */
  .bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 36px;
  }

  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: $dark;
  }

  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: lighten($color: $dark, $amount: 40);
  }

  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }

  /* Color/shape of close button cross */
  .bm-cross {
    background: $light;
  }

  /*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }

  /* General sidebar styles */
  .bm-menu {
    background: #373a47;
    padding: 2.5em 2em 0;
    font-weight: 300;
    @include typo-size($main_font, map-get($font-sizes, xs));
  }

  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: $dark;
  }

  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 1em;
  }

  /* Individual item */
  .bm-item {
    display: inline-block;
    color: $light;
    margin-bottom: 20px;
    text-align: left;
    text-decoration: none;
    transition: color 0.2s;
  }

  .bm-item:hover {
    color: #ffffff;
  }

  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
  @media (min-width: map-get($breakpoints, tablet)) {
    .bm-burger-button,
    .bm-burger-bars,
    .bm-cross-button,
    .bm-item,
    .bm-item-list,
    .bm-menu-wrap {
      display: none;
    }
  }
}
